import styled from "styled-components";
import { WHITE, SILVER_GREY } from "../../utils/colors";

const Footer = styled.footer`
  background-color: ${WHITE};
  border-top: 1.5px solid ${SILVER_GREY};
  min-height: 4rem;
  padding: 0 2rem;
`;

export default Footer;



