import styled from "styled-components";
import { WHITE, SILVER_GREY } from "../../utils/colors";

const Header = styled.header`
  background-color: ${WHITE};
  border-bottom: 2px solid ${SILVER_GREY};
  min-height: 5rem;
  padding: 0 2rem;
`;

export default Header;



